\n      color: $lightHover;\r\n    }\r\n  }\r\n  .form-control {\r\n    background-color: $input;\r\n    border-color: $input;\r\n    color: white;\r\n    &:focus {\r\n      box-shadow: 0 0 0 0.25rem rgb(60 65 72 / 25%);\r\n    }\r\n    &::placeholder {\r\n      color: $textPale;\r\n    }\r\n    &.is-invalid {\r\n      border-color: $form-feedback-invalid-color !important;\r\n    }\r\n  }\r\n  .form-check-input {\r\n    background-color: $input;\r\n  }\r\n  .bg-box {\r\n    background-color: $screenComments;\r\n  }\r\n  .btn-approve {\r\n    background: $success;\r\n    color: #fff;\r\n  }\r\n  .btn-dark-blue {\r\n    background-color: $buttons;\r\n    border-color: $buttons;\r\n    color: $buttonsText;\r\n    &:hover {\r\n      background-color: $buttonsHover;\r\n      border-color: $buttonsHover;\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n    &:focus {\r\n      box-shadow: 0 0 0 0.25rem rgb(84 96 113 / 15%);\r\n    }\r\n    &:active {\r\n      background-color: lighten($buttons, 10%);\r\n      border-color: lighten($buttons, 10%);\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n  }\r\n  .btn-outline-light {\r\n    border-color: $buttonsOutline;\r\n    color: $buttonsText;\r\n    &:hover {\r\n      background-color: $buttonsHover;\r\n      border-color: $buttonsHover;\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n    &:focus {\r\n      box-shadow: 0 0 0 0.25rem rgb(84 96 113 / 15%);\r\n    }\r\n    &:active {\r\n      background-color: lighten($buttons, 10%);\r\n      border-color: lighten($buttons, 10%);\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n  }\r\n  .bg-dark-blue {\r\n    background-color: $block;\r\n    color: $light;\r\n  }\r\n  .bg-light-blue {\r\n    background-color: $light;\r\n    color: #000;\r\n  }\r\n  .text-pale {\r\n    color: $textPale !important;\r\n  }\r\n  .bg-input {\r\n    background-color: $input;\r\n    border-color: $input !important;\r\n    color: white;\r\n  }\r\n  .modal {\r\n    background-color: #000000ab;\r\n    color: $light;\r\n    .modal-content {\r\n      background-color: $block;\r\n    }\r\n    &.show {\r\n      display: block;\r\n    }\r\n  }\r\n  .dropdown-item,\r\n  a.dropdown-item {\r\n    color: $light;\r\n  }\r\n  .pointer {\r\n    cursor: pointer;\r\n  }\r\n  .form-check-input.input-switch {\r\n    &:checked {\r\n      background-color: $switch;\r\n      border-color: $switch;\r\n    }\r\n  }\r\n  .popover-block {\r\n    position: relative;\r\n    .popover {\r\n      top: -64px;\r\n      left: 15px;\r\n      .popover-arrow {\r\n        transform: translate(10px, 0px);\r\n        &::after {\r\n          border-top-color: $informers !important;\r\n          border-right-color: $informers !important\r\n        }\r\n      }\r\n      .popover-body {\r\n        background: $informers;\r\n      }\r\n    }\r\n  }\r\n  .bi {\r\n    width: 1em;\r\n    height: 1em;\r\n    vertical-align: -0.125em;\r\n  }\r\n  .fs-small {\r\n    font-size: 0.8rem !important;\r\n  }\r\n}","@import \"/commentzone/resource/style/main-panel.scss\";\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\r\n#commentzone {\r\n  .cz-images-view-block {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    background-color: #00000066;\r\n    z-index: 999;\r\n    .cz-images-view-panel-control {\r\n      position: absolute;\r\n      bottom: 0;\r\n      left: 0;\r\n      width: 100%;\r\n      .cz-btn {\r\n        font-size: 36px;\r\n        line-height: 1.45;\r\n        width: 60px;\r\n        height: 60px;\r\n        background: #101010;\r\n        color: #fff;\r\n        text-align: center;\r\n        cursor: pointer;\r\n        user-select: none;\r\n      }\r\n    }\r\n    .cz-image