ary, 18%) !important;\r\n}\r\n.bg-pale {\r\n  background-color: $input !important;\r\n}\r\n.border-pale {\r\n  border-color: $input !important;\r\n}\r\n\r\n#commentzone {\r\n  position: fixed;\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow: auto;\r\n  background-color: $backgroundBody !important;\r\n  color: $light;\r\n  a {\r\n    color: $light;\r\n    &:hover,\r\n    &.active {\r\n      color: $lightHover;\r\n    }\r\n  }\r\n  .form-control {\r\n    background-color: $input;\r\n    border-color: $input;\r\n    color: white;\r\n    &:focus {\r\n      box-shadow: 0 0 0 0.25rem rgb(60 65 72 / 25%);\r\n    }\r\n    &::placeholder {\r\n      color: $textPale;\r\n    }\r\n    &.is-invalid {\r\n      border-color: $form-feedback-invalid-color !important;\r\n    }\r\n  }\r\n  .form-check-input {\r\n    background-color: $input;\r\n  }\r\n  .bg-box {\r\n    background-color: $screenComments;\r\n  }\r\n  .btn-approve {\r\n    background: $success;\r\n    color: #fff;\r\n  }\r\n  .btn-dark-blue {\r\n    background-color: $buttons;\r\n    border-color: $buttons;\r\n    color: $buttonsText;\r\n    &:hover {\r\n      background-color: $buttonsHover;\r\n      border-color: $buttonsHover;\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n    &:focus {\r\n      box-shadow: 0 0 0 0.25rem rgb(84 96 113 / 15%);\r\n    }\r\n    &:active {\r\n      background-color: lighten($buttons, 10%);\r\n      border-color: lighten($buttons, 10%);\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n  }\r\n  .btn-outline-light {\r\n    border-color: $buttonsOutline;\r\n    color: $buttonsText;\r\n    &:hover {\r\n      background-color: $buttonsHover;\r\n      border-color: $buttonsHover;\r\n      color: lighten($buttonsText, 12%);\r\n    }\r\n    &:focus {\r\n      box-shadow: 0 0 0 0.25rem rgb(84 96 113 / 15%);\r\n    }\r\n    &:active {\r\n      background-color: lighten($buttons, 10%);\r\n      border-color: lighten($buttons, 10%);\r\n      color: lighten($buttonsText, 12%);\r\n  